import React from "react";
import Footer from "./FooterIndex";
import { useHistory } from "react-router-dom";
import { webDomain } from "../../commonApi/Link";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
    faFacebook,
    faInstagram,
    faTwitter,
} from "@fortawesome/free-brands-svg-icons";
import { faLocationArrow, faPhone } from "@fortawesome/free-solid-svg-icons";

function FooterContainer() {
    const history = useHistory();
    return (
        <Footer>
            <Footer.Wrapper>
                <Footer.Row>
                    <Footer.LogoColumn>
                        <Footer.Logo
                            src={`${webDomain}/assests/makerkowhite.png`}
                            alt="logo"
                            preview=""
                        />
                    </Footer.LogoColumn>
                    <Footer.Column>
                        <Footer.Title>Explore</Footer.Title>
                        <Footer.Link
                            onClick={() => {
                                history.push({
                                    pathname: "/",
                                    state: { toScroll: "aboutUs" },
                                });
                            }}
                        >
                            About
                        </Footer.Link>
                        <Footer.Link
                            onClick={() => {
                                history.push({ pathname: "/makers" });
                            }}
                        >
                            Makers
                        </Footer.Link>
                        <Footer.Link
                            onClick={() => {
                                history.push({ pathname: "/projects" });
                            }}
                        >
                            Projects
                        </Footer.Link>
                        <Footer.Link
                            onClick={() => {
                                history.push({ pathname: "/blogs" });
                            }}
                        >
                            Blog
                        </Footer.Link>
                        <Footer.Link>Get A Quote</Footer.Link>
                    </Footer.Column>
                    <Footer.Column>
                        <Footer.Title>Unique Services</Footer.Title>
                        <Footer.Link>Prototyping</Footer.Link>
                        <Footer.Link>Design Support</Footer.Link>
                        <Footer.Link>Local Production</Footer.Link>
                    </Footer.Column>
                    <Footer.Column>
                        <Footer.Title>Socials</Footer.Title>
                        <Footer.Link
                            href="https://www.facebook.com/makerko.np"
                            target="_blank"
                        >
                            <FontAwesomeIcon
                                icon={faFacebook}
                                size="lg"
                                style={{ marginRight: "5px" }}
                            />
                            Facebook
                        </Footer.Link>
                        <Footer.Link
                            href="https://www.instagram.com/makerko.np/"
                            target="_blank"
                        >
                            <FontAwesomeIcon
                                icon={faInstagram}
                                size="lg"
                                style={{ marginRight: "5px" }}
                            />
                            Instagram
                        </Footer.Link>
                        <Footer.Link href="https://twitter.com" target="_blank">
                            <FontAwesomeIcon
                                icon={faTwitter}
                                size="lg"
                                style={{ marginRight: "5px" }}
                            />
                            Twitter
                        </Footer.Link>
                    </Footer.Column>
                    <Footer.Column>
                        <Footer.Title>Contact and Support</Footer.Title>
                        <Footer.Link>
                            <FontAwesomeIcon
                                icon={faPhone}
                                size="lg"
                                style={{ marginRight: "5px" }}
                            />
                            +977-9851206487
                        </Footer.Link>
                        <Footer.Link>
                            {" "}
                            <FontAwesomeIcon
                                icon={faLocationArrow}
                                size="lg"
                                style={{ marginRight: "5px" }}
                            />
                            makerkodesigns@gmail.com Lalitpur-44700, Nepal
                        </Footer.Link>
                        <Footer.Link href="#">Give Feedback</Footer.Link>
                    </Footer.Column>
                </Footer.Row>
                <Footer.Horizontal />
                <div className="pl-3 pr-3">
                    <Footer.Copyright>
                        Copyright(c) Makerko 2021
                    </Footer.Copyright>
                    <Footer.TermsAndPolicy>
                        <Footer.TermsPolicy href="#">
                            Privacy Policy
                        </Footer.TermsPolicy>{" "}
                        |{" "}
                        <Footer.TermsPolicy href="#">
                            Terms of Use
                        </Footer.TermsPolicy>
                    </Footer.TermsAndPolicy>
                </div>
            </Footer.Wrapper>
        </Footer>
    );
}

export default FooterContainer;
